.main {
    width: 90%;
    padding: 20px 10px;
    margin: 0px auto;
    margin-top: 10rem;
  }
  .greet-main {
    width: 90%;
    padding: 20px 10px;
    margin: 0px auto;
    margin-top: 10rem;
  }
  
  .gretting-main {
    width: 90%;
    padding: 20px 10px;
    margin: 0px auto;
  }
  
  .greeting-main {
    display: flex;
  }
  
  .greeting-main > * {
    flex: 1;
    margin-bottom: 30px;
  }
  .button-greeting-div {
    display: flex;
    margin-top: 20px;
  }
  
  .greeting-text {
    font-size: 70px;
    line-height: 1.1;
    font-family: "Inter", sans-serif;
    font-weight: 900;
  }
  
  .greeting-text-p {
    font-size: 25px;
    line-height: 25px;
    font-family: "Inter", sans-serif;
    color: black;
  }

  .greeting-text-p2 {
    font-size: 20px;
    line-height: 25px;
    font-family: "Inter", sans-serif;
    color: gray;
  }


  .greeting-image-div > img {
    max-width: 100%;
    height: auto;
  }
  .wave-emoji {
    animation-duration: 1.8s;
    animation-iteration-count: infinite;
    animation-name: wave;
    display: inline-block;
    transform-origin: 70% 70%;
  }
  
  @keyframes wave {
    0% {
      transform: rotate(0deg);
    }
    10% {
      transform: rotate(-10deg);
    }
    20% {
      transform: rotate(12deg);
    }
    30% {
      transform: rotate(-10deg);
    }
    40% {
      transform: rotate(9deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  /* Media Query */
  @media (max-width: 1380px) {
    .greeting-text {
      font-size: 50px;
    }
    .greeting-text-p {
      font-size: 20px;
    }
  }
  @media (max-width: 768px) {
    .button-greeting-div {
      justify-content: space-around;
    }
    .greeting-text {
      font-size: 30px;
      text-align: center;
    }
    .greeting-text-p {
      font-size: 16px;
      line-height: normal;
      text-align: center;
    }
    .greeting-main {
      display: block;
    }
  }